.name{
  color: #FACF0F;
}
.container{
  display:flex;
  flex-direction: row-reverse;
  margin: 7rem 2rem 5rem 2rem;
}

.heading{
  display: inline;
}
.btn{
  transition: all .5s ease .1s;
}

.btn:hover{
  background-color: white;
  color:#FACF0F;
  border: 1px solid #FACF0F;
}

.linkandgit{
  display: flex;
  justify-content: center;
  font-size: 2rem;
  margin-top: 2rem;
}

.icongit{
  margin:0  5%;   
}

.image{
  width: 60%;
  object-fit: cover;
  border-radius: 10%;
  display: inline-block;
  margin: auto auto;
  border: 3px solid #FACF0F;  
}