.container{
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
  background-color: #FAFAFA;
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 100;
}

.navbar{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 2rem;
}

.subtitle{
  font-size: 1rem;
  font-weight: 600;
  padding: 0 20px;
  display: inline-block;
  transition: all .5s ease-in-out ;
  position: relative;   
}

.box:hover .subtitle:not(:hover){
  opacity: 0.2;
  filter: blur(1px);
}

.subtitle:hover{  
  opacity: 1;
  filter: blur(0);
  transform: scale(1.2);
  color: #FAFAFA; 
}

.subtitle::before{
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #FACF0F;
  transition: transform 0.5s ease-out;
  transform-origin: right;
  transform: scaleX(0);
  z-index: -1;
}

.subtitle:hover:before{
  transform-origin: left;
  transform: scaleX(1);
  transition: transform 0.5s ease-in; 
}

.add{
  font-size: 1.2rem;
  font-weight: 600;
  padding: 0 20px;
  display: inline-block;
  transform: scale(1.5);
  text-decoration: underline;
  text-decoration-color: #FACF0F;
}

.subtitle1{
  font-size: 1.2rem;
  font-weight: 400;
  text-align: left;
  padding: 10px 20px 20px 0;
  display:block;
  transition: all .5s ease-in-out;
}

.subtitle1:hover {
  text-decoration: underline;
  text-decoration-color: #FACF0F;
}

.logo{
  font-size: 3rem;
  font-weight: 700;
}

.container1{
  background-color: #FAFAFA;
  padding: 20px;
  height: auto;
  width: auto;
}

.show{
  z-index: 10000;
  border-radius: 15px;
  background-color: #FAFAFA;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  position: fixed;
  top:0;
  right:0;
  visibility: hidden;
  height: 100%;
  padding: 20px 50px 50px 50px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
}

.icons{
  display: inline;
  margin-right: 10px;
}

.icon{
  cursor: pointer;
  font-size: 2rem;
  display: none;
}

.icon2{
  cursor: pointer;
  position: relative;
  right: -100%;
  font-size: 2rem;
}

.scrollUp{
  position: fixed;
  bottom: -3rem;
  right: 0;
  height: 100px;
  width: 100px;
}

